import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import "./index.css"
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faCartArrowDown, faTrashCan } from '@fortawesome/free-solid-svg-icons'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'

library.add(faCartArrowDown, faTrashCan)

createApp(App).use(ElementPlus).use(store).use(router).component('font-awesome-icon', FontAwesomeIcon).mount('#app')

